.order_info_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  width: 640px;
  margin: 0 auto;
}

.order_number {
  margin: 0 auto;
}

.order_name {
  text-align: start;
}

.ready {
  color: #00CCCC;
  text-align: start;
}

.pending {

}

.ingredients_wrapper {
  padding: 0 24px 0 0;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
  list-style-type: none;
  margin-bottom: 40px;
  overflow: auto;
  max-height: 480px;
}

.ingredients_wrapper::-webkit-scrollbar {
  width: 8px;
}

.ingredients_wrapper::-webkit-scrollbar-track {
  background-color: #2f2f37;
}

.ingredients_wrapper::-webkit-scrollbar-thumb {
  background-color: #8585AD;
  height: 350px;
}

.ingredient_wrapper {
  display: flex;
  text-decoration: none;
  gap: 16px;
  margin-bottom: 16px;
}

.name {
  flex-grow: 1;
  text-align: start;
}

.cost {
  display: flex;
  text-align: center;
  align-self: center;
}

.currency {
  align-self: center;
  margin-left: 8px;
}

.info_wrapper {
  display: flex;
  justify-content: space-between;
}

.price_wrapper{
  display: flex;
  gap: 8px;
}